import InputCell from '@/components/InputCell/InputCell.vue'
export default {
	data() {
		return {
			isEdit: true, // 是否编辑状态
			avatar: '../../../static/img/avatar.png', //头像地址
			resume: {
				name: '',
				sex:　'',
				age: '',
				education: '',
				phone: '',
				exprience: '',
				introduction: '',
				university: '',
				nativePlace: ''
			},
			pictureArray: ['从相册选择', '相机拍摄'],
			pictureIndex: 0
		}
	},
	components: {
		InputCell
	},
	onLoad(options){
		if(options.isEdit === 'false'){
			this.isEdit = false
		}
		if(options.isEdit === 'true'){
			this.isEdit = true
		}
	},
	methods:{
		chooseImage(){
			// 让用户选择相册或者拍照
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.avatar = res.tempFilePaths[0]
				}
			})
		},
	}
}
